<template>
	<view class="container">
		<view class="carousel">
			<swiper indicator-dots circular=true duration="400">
				<swiper-item class="swiper-item" v-for="(item,index) in imgList" :key="index">
					<view class="image-wrapper">
						<image :src="item" class="loaded" mode="aspectFill" ></image>
					</view>
				</swiper-item>
			</swiper>
		</view>
		
		<view class="introduce-section">
			<view class="title">{{bydata.title}}</view>
			<view>
				<text class="ftitle">{{bydata.fTitle}}</text>
			</view>
			
			<view class="price-box">
				<text class="price-tip">¥</text>
				<text class="price">{{specList.price}}</text>
				<!-- <text class="m-price">¥488</text>
				<text class="coupon-tip">7折</text> -->
			</view>
			<view class="bot-row">
				<text>销量: {{bydata.aid}}</text>
				<text>库存: {{stock}}</text>
				<text>浏览量: {{bydata.bid}}</text>
			</view>
		</view>
		
		
		
		<view class="c-list">
			<view class="c-row b-b" @click="toggleSpec">
				<text class="tit">购买类型</text>
				<view class="con">
					<text class="selected-text" >
						{{name}} / {{specList.name}}
					</text>
				</view>
				<text class="yticon icon-you"></text>
			</view>
			<view class="c-row b-b">
				<text class="tit">购买数量</text>
				<text class="con t-r " ></text>
				<text class="con t-r " ></text>
				<text class="con t-r " ></text>
				<text class="con t-r jishuq" @click="addReduce(0)">-</text>
				<text class="con t-r jishuq" >{{mun}}</text>
				<text class="con t-r jishuq" @click="addReduce(1)">+</text>
			</view>
			<view class="c-row b-b">
				<text class="tit">运费</text>
				<text class="con t-r " >{{bydata.freight == 0 ? "包邮":bydata.freight}}</text>
			</view>
			<!-- <view class="c-row b-b">
				<text class="tit">促销活动</text>
				<view class="con-list">
					<text>新人首单送20元无门槛代金券</text>
					<text>订单满50减10</text>
					<text>订单满100减30</text>
					<text>单笔购买满两件免邮费</text>
				</view>
			</view> -->
			<view class="c-row b-b">
				<text class="tit">服务</text>
				<view class="bz-list con">
					<text>7天无理由退换货 ·</text>
				</view>
			</view>
		</view>
		
		
		<view class="detail-desc">
			<view class="d-header">
				<text>图文详情</text>
			</view>
			<!-- <rich-text :nodes="desc"></rich-text> -->
			<view v-for="(item,index) in desc" :key="index">
				<image :src="item" class="xasd" mode="aspectFill" ></image>
			</view>
		</view>
		
		<!-- 底部操作菜单 -->
		<view class="page-bottom">
			<navigator url="/pages/index/index" open-type="switchTab" class="p-b-btn">
				<text class="yticon icon-xiatubiao--copy"></text>
				<text>首页</text>
			</navigator>
			<navigator url="/pages/cart/cart" open-type="switchTab" class="p-b-btn">
				<text class="yticon icon-gouwuche"></text>
				<text>购物车</text>
			</navigator>
			<view class="p-b-btn" >
				<!-- <text class="yticon icon-shoucang"></text> -->
				<button class="yticon icon-dianhua-copy zkf" open-type='contact' session-from=''></button>
				<text>客服</text>
			</view>
			
			<view class="action-btn-group">
				<button type="primary" class=" action-btn no-border buy-now-btn" @click="buy(1)">立即购买</button>
				<button type="primary" class=" action-btn no-border add-cart-btn" @click="buy(0)">加入购物车</button>
			</view>
		</view>
		
		
		<!-- 规格-模态层弹窗 -->
		<view 
			class="popup spec" 
			:class="specClass"
			@touchmove.stop.prevent="stopPrevent"
			@click="toggleSpec"
		>
			<!-- 遮罩层 -->
			<view class="mask"></view>
			<view class="layer attr-content" @click.stop="stopPrevent">
				<view class="a-t">
					<image :src="bydata.ioc"></image>
					<view class="right">
						<text class="price">¥{{specList.price}}</text>
						<text class="stock">库存：{{specList.stock}}件</text>
						<view class="selected">
							已选：
							<text class="selected-text" >
								{{name}} / {{specList.name}}
							</text>
						</view>
					</view>
				</view>
				<view class="item-list">
					<text 
						v-for="(childItem, childIndex) in specChildList" 
						:key="childIndex" class="tit"
						:class="{selected: childItem.id == specList.specsid}"
						@click="selectSpec(childIndex)"
					>
						{{childItem.name}}
					</text>
				</view>
				<view class="item-list">
					<text 
						v-for="(childItem, childIndex) in shareList" 
						:key="childIndex" class="tit"
						:class="{selected: childItem.id == specList.id}"
						@click="selectSpecs(childIndex)"
					>
						{{childItem.name}}
					</text>
				</view>	
					
					
				<button class="btn" @click="toggleSpec">完成</button>
			</view>
		</view>
		
	</view>
</template>

<script>
	import api from '../../common/api.js'
	export default{
		components: {
		},
		data() {
			return {
				id:0,
				stock:0,
				name:'',
				mun:1,
				specClass: 'none',
				specSelected:[],
				favorite: true,
				bydata:{},
				shareList: [],
				imgList: [],
				desc: [],
				specList: {},
				specChildList: []
			};
		},
		async onLoad(options){
			
			this.id=options.id;
			
			this.list();
		},
		methods:{
			//规格弹窗开关
			toggleSpec() {
				if(this.specClass === 'show'){
					this.specClass = 'hide';
					setTimeout(() => {
						this.specClass = 'none';
					}, 250);
				}else if(this.specClass === 'none'){
					this.specClass = 'show';
				}
			},
			//选择规格
			selectSpec(index){
				let list = this.specChildList;
				this.name=list[index].name;
				this.specList=list[index].list[0];
				this.shareList=list[index].list;
				// console.log(list[index])
				
			},
			//选择规格
			selectSpecs(index){
				let list = this.shareList;
				this.specList=list[index];
				
			},
			addReduce(id) {
				
				if(id == 0) {
					console.log(id)
					if(this.mun > 1) {
						this.mun=this.mun-1;
					}
				}else {
					if(this.mun < this.specList.stock){
						this.mun=this.mun+1;
					}
				}
			},			
			list(){
				api.post('Goods/xcxgoodsdata', {"id":this.id}).then(res => {
					this.imgList=res.data.imglist.split(',');
					this.desc=res.data.details.split(',');
					this.bydata=res.data;
					// console.log(res)
				})
				api.post('GoodsSpecsFit/list', {"goodsid":this.id}).then(res => {
					this.specChildList=res.data
					// console.log(res)
					let stock=0;
					for(let cItem of res.data){
						for(let bItem of cItem.list){
							stock=stock+bItem.stock;	
						}
					}
					this.stock=stock;
					this.specList=res.data[0].list[0];
					this.shareList=res.data[0].list;
					this.name=res.data[0].name
					// console.log(res.data[0].list[0])
					
				})
			},
			buy(e){
				let userInfo=uni.getStorageSync('userInfo');
				if(userInfo){
					let data={
						  "goodsid":this.id,
						  "specsid":this.specList.specsid,
						  "specsname":this.name, 
						  "fitid":this.specList.id, 
						  "fitname":this.specList.name, 
						  "price":this.specList.price, 
						  "ioc":this.bydata.ioc, 
						  "title":this.bydata.title, 
						  "freight":this.bydata.freight,
						  "eliminate":e,
						  "mun":this.mun,
						  "uid":userInfo.id
					}
					
					console.log(data)
					api.post('Cart/add', data).then(res => {
						
						if(e==1){
							uni.navigateTo({
								url: `/pages/order/createOrder?id=`+res.data
							})
						}
						
						// console.log(res)
					})
					
					// uni.navigateTo({
					// 	url: `/pages/order/createOrder`
					// })
				}else{
					uni.showToast({
						title: '用户未登录！请先登录',
						icon: 'none'
					});
				}
				
			},
			stopPrevent(){}
		},

	}
</script>

<style lang='scss'>
